.team {
  background: url("../images/bg/SBS_1255-X2.jpg") center center / cover no-repeat;
  position: relative;
}

.container--team {
  position: absolute;
  width: 50%;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #2f3234;
  opacity: 0.93;
  display: flex;
  flex-direction: column;
  align-items: center;

  @include tablets {
    left: 0;
    width: 100%;
  }
}

.team__content {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 14% 16%;

  @include tablets {
    text-align: center;
  }

  @include phones {
    margin: 90px 65px;
  }
}

.team-acc {
  width: 100%;

  @include phones {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__item {
    width: 100%;

    @include tablets {
      display: flex;
      flex-direction: column;
      margin-bottom: 8%;
    }

    @include phones {
    //  margin-bottom: 20px;
    }
  }

  &__item-trigger {
    background-color: transparent;
    border: transparent;
    color: #ffffff;
    font-family: 'Gagalin', Impact, sans-serif;
    font-size: rem(14px);
    font-weight: 400;
    line-height: rem(28.13px);
    display: flex;
    align-items: center;

    @include tablets {
      justify-content: center;
      font-size: rem(25px);
    }

    &:before {
      content: "";
      width: 0;
      height: 0;
      border: 7px solid transparent;
      border-top-color: $yellow;
      border-bottom-width: 0;
      transition: all .5s;
      margin-right: 10px;
    }
  }

  &__desc {
    width: 100%;
    height: 0;
    overflow: hidden;
    display: flex;
    margin-top: 17px;

    @include tablets {
      flex-direction: column;
      align-items: center;
    }

    @include phones {
      padding-top: 10px;
    }
  }

  &__pic {
    width: 100px;
    height: 100px;
    margin-right: 21px;
    margin-bottom: 40px;

    @include tablets {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }

  &__pic-img {
    max-width: 100px;
    height: 100px;
    border-radius: 50%;
    // overflow: hidden;
    object-fit: cover;
  }

  &__text {
    // max-width: 330px;
    display: flex;
    flex-direction: column;
    font-size: rem(14px);
    color: #ffffff;

    &-title {
      font-weight: 700;
    }

    &-content {
      width: 100%;
      font-weight: 300;
      line-height: rem(24.01px);
    }
  }
}

.team-acc__desc {
  min-height: 0;
  transition: min-height 1s;
}

.active {
  .team-acc__item-trigger {
    color: $yellow;

    &:before {
      transform: rotate(-180deg);
      transition: .5s;
    }
  }

  .team-acc__desc {
    min-height: 101px;

    @include tablets {
      min-height: 200px;
    }
  }
}